import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import {
  Form,
  Tabs,
  Table,
  Button,
  Modal,
  Row,
  Col,
  message,
  Upload,
  Tooltip,
  Popconfirm,
  Select,
  Icon,
  Alert,
  Input,
  Spin,
  Typography,
} from 'antd';

import { injectIntl } from 'react-intl';
import {
  getProjects,
  getProcessTypes,
  addJob,
  getJobs,
  saveJob,
  deleteJob,
  getClients,
} from '../../services';
import functions from './../../utils/functions';

const { Option } = Select;
const { TabPane } = Tabs;
const Dragger = Upload.Dragger;
const FormItem = Form.Item;
const { Title, Text } = Typography;

const JobsCmp = props => {
  const {
    user,
    match: { params },
    form,
    form: { getFieldDecorator },
    intl: { messages },
  } = props;

  const [visible, setVisible] = useState(false);
  const [loading, setLoading] = useState(false);
  const [ploading, setPloading] = useState(false);
  const [jobs, setJobs] = useState([]);
  const [job, setJob] = useState(null);

  const [projects, setProjects] = useState([]);
  const [project, setProject] = useState(null);
  const [processTypes, setProcessTypes] = useState([]);
  const [mode, setMode] = useState('add');

  const [fileList, setFileList] = useState([]);
  const [showError, setShowError] = useState(false);
  const [clients, setClients] = useState([]);

  useEffect(() => {
    fetch();
    fetchProjects();
    fetchProcessType();
    if (user && user.rol === 'admin') {
      fetchClients();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetch = async () => {
    try {
      setLoading(true);
      const { data } = await getJobs();
      setJobs(data.jobs);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const fetchProjects = async () => {
    try {
      setPloading(true);
      const { data } = await getProjects();
      setProjects(data.projects);
      setPloading(false);
    } catch (error) {
      setPloading(false);
      message.error(error.message);
    }
  };

  const fetchProcessType = async () => {
    try {
      const { data } = await getProcessTypes();
      setProcessTypes(data);
    } catch (error) {
      message.error(error.message);
    }
  };

  const fetchClients = async () => {
    try {
      const { data } = await getClients();
      setClients(data.clients);
    } catch (error) {
      message.error(error.message);
    }
  };

  const remove = async record => {
    try {
      setLoading(true);
      await deleteJob(record.id);
      fetch();
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const getSubProcessType = value => {
    const pp = projects.filter(item => item.id === value);

    if (pp && pp.length) {
      const aux = processTypes.filter(item => item.id === pp[0].ProcessTypeId);

      if (aux && aux.length) {
        return aux[0].SubprocessTypes;
      }
    }
    return [];
  };

  const getProjectValue = value => {
    const pp = projects.filter(item => item.id === value);
    if (pp && pp.length) {
      return pp[0].name;
    }
    return null;
  };

  const getProjectValueV1 = () => {
    const pp = projects.filter(item => item.id === parseInt(params.projectId));
    if (pp && pp.length) {
      return pp[0];
    }
    return null;
  };

  const getClientsValue = client => {
    if (user.rol === 'admin') {
      const pp = clients.filter(item => item.id === client);
      if (pp && pp.length) {
        return pp[0];
      }
    }
    if (user.rol === 'client') {
      return user;
    }

    return null;
  };

  const handleChangeProject = value => {
    setProject(value);
  };

  const handleReset = () => {
    form.resetFields();
  };

  const handleSubmit = e => {
    e.preventDefault();
    if (!fileList.length) {
      setShowError(true);
    }

    if (fileList.length || mode === 'edit') {
      form.validateFields(async (err, values) => {
        if (!err) {
          setLoading(true);
          try {
            if (mode === 'add') {
              let form = {
                name: values.name,
                projectId: project,
                subprocess: values.subprocess,
                src: values.src,
                tgt: values.tgt,
                files: fileList,
              };
              await addJob(form);
            }
            if (mode === 'edit') {
              let form = {
                name: values.name,
              };
              await saveJob(job.id, form);
            }

            message.success(messages.default.success);
            handleReset();
            setVisible(false);
            fetch();
          } catch (error) {
            setVisible(true);
            setLoading(false);
            message.error(error.message);
          }
        }
      });
    }
  };

  const showModal = value => {
    handleReset();
    setMode(value);
    setVisible(true);
  };

  const columns = [
    {
      title: 'Id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Filename',
      dataIndex: 'filename',
      key: 'filename',
    },
    {
      title: 'Project',
      dataIndex: 'ProjectId',
      key: 'ProjectId',
      render: value => {
        const aux = projects.filter(item => item.id === value);
        if (aux && aux.length) {
          return <span>{aux[0].name}</span>;
        } else {
          return <span>---</span>;
        }
      },
    },
    {
      title: 'Subprocess',
      dataIndex: 'SubprocessType',
      key: 'SubprocessType',
      render: value => {
        return <div>{value.name}</div>;
      },
    },
    {
      title: 'Src/Tgt',
      key: 'srctgt',
      render: record => {
        return (
          <span>
            {record.src} - {record.tgt}
          </span>
        );
      },
    },
    {
      title: '',
      key: 'actions',
      width: 100,
      render: (value, record) => {
        return (
          <React.Fragment>
            <Tooltip placement="top" title="Edit Job">
              <Button
                style={{ marginRight: 5 }}
                onClick={() => {
                  setJob(record);
                  showModal('edit');
                }}
                size="small"
                type="primary"
                icon="edit"
              />
            </Tooltip>
            <Popconfirm
              onConfirm={() => {
                remove(record);
              }}
              title="Are you sure?"
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <Tooltip placement="top" title="Delete Job">
                <Button size="small" type="danger" icon="delete" />
              </Tooltip>
            </Popconfirm>
          </React.Fragment>
        );
      },
    },
  ];

  const operations = (
    <React.Fragment>
      <Button
        loading={loading}
        onClick={() => {
          showModal('add');
        }}
        type="primary"
        icon="smile"
      >
        Add
      </Button>
    </React.Fragment>
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  const formItemLayout2 = {
    labelCol: {
      xs: { span: 24 },
    },
    wrapperCol: {
      xs: { span: 24 },
    },
  };

  const dprops = {
    onRemove: file => {
      const index = fileList.indexOf(file);
      const newFileList = fileList.slice();
      newFileList.splice(index, 1);
      setFileList(newFileList);
    },

    beforeUpload: file => {
      const isLt2M = file.size / 1024 / 1024 < 500;
      if (!isLt2M) {
        message.error(messages.process.error.uploadTwo);
      }
      return false;
    },
    onChange: info => {
      let aux = [];
      // info.fileList.map(item => {
      //   aux.push(item);
      //   return aux;
      // });
      aux.push(info.file);
      setFileList(aux);
    },
    multiple: false,
    fileList,
  };

  return (
    <Spin spinning={loading || ploading}>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        {params.projectId && (
          <React.Fragment>
            <Title level={4}>
              <Text underline>Project:</Text>{' '}
              {getProjectValueV1() ? getProjectValueV1().name : null}
            </Title>
            <Row>
              <Col>
                {getProjectValueV1() ? (
                  <div>
                    <strong>
                      <Icon type="user" /> Client:{' '}
                    </strong>
                    {getClientsValue(getProjectValueV1().UserId) && (
                      <React.Fragment>
                        {getClientsValue(getProjectValueV1().UserId).fullName}{' '}
                        <small>({getClientsValue(getProjectValueV1().UserId).email})</small>
                      </React.Fragment>
                    )}
                  </div>
                ) : null}
              </Col>
              <Col>
                {getProjectValueV1() && getProjectValueV1().Actor ? (
                  <div>
                    <strong>
                      <Icon type="smile" /> PM:{' '}
                    </strong>
                    {getProjectValueV1().Actor.name}{' '}
                    <small>({getProjectValueV1().Actor.email})</small>
                  </div>
                ) : null}
              </Col>
              <Col>
                {getProjectValueV1() ? (
                  <div>
                    <strong>
                      <Icon type="interaction" /> Process Type :{' '}
                    </strong>
                    {getProjectValueV1().ProcessType.name}{' '}
                  </div>
                ) : null}
              </Col>
            </Row>
          </React.Fragment>
        )}
        <Tabs defaultActiveKey="all" tabBarExtraContent={operations}>
          <TabPane tab="Jobs" key="all">
            <Table
              size="small"
              loading={loading}
              columns={columns}
              dataSource={jobs}
              rowKey={record => record.id}
            />
          </TabPane>
        </Tabs>

        <Modal
          centered
          width={600}
          title="Job Form"
          visible={visible}
          onCancel={() => {
            setVisible(false);
          }}
          footer={[
            <Button
              key="back"
              onClick={() => {
                setVisible(false);
              }}
            >
              {messages.default.cancel}
            </Button>,
            <Button key="submit" type="primary" loading={loading} onClick={handleSubmit}>
              {mode === 'add' ? messages.default.create : messages.default.save}
            </Button>,
          ]}
        >
          <Form {...formItemLayout}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label="Name" hasFeedback>
                  {getFieldDecorator('name', {
                    initialValue: job && job.name ? job.name : '',
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.node_id.required,
                      },
                    ],
                  })(<Input />)}
                </FormItem>

                <FormItem label="Project" hasFeedback>
                  {getFieldDecorator('projectId', {
                    initialValue: job && job.ProjectId ? getProjectValue(job.ProjectId) : '',
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.model_id.required,
                      },
                    ],
                  })(
                    <Select onChange={handleChangeProject} disabled={mode === 'edit'}>
                      {projects.length > 0 &&
                        projects.map(item => <Option value={item.id}>{item.name}</Option>)}
                    </Select>
                  )}
                </FormItem>

                {(project || (job && job.ProjectId)) && (
                  <FormItem label="Subprocess" hasFeedback>
                    {getFieldDecorator('subprocess', {
                      initialValue: job && job.SubprocessType ? job.SubprocessType.name : '',
                      rules: [
                        {
                          required: true,
                          // message: messages.eds.rules.model_id.required,
                        },
                      ],
                    })(
                      <Select disabled={mode === 'edit'}>
                        {getSubProcessType(project).length > 0 &&
                          getSubProcessType(project).map(item => (
                            <Option value={item.id}>{item.name}</Option>
                          ))}
                      </Select>
                    )}
                  </FormItem>
                )}

                <FormItem label="Source" hasFeedback>
                  {getFieldDecorator('src', {
                    initialValue: job && job.src ? job.src : '',
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.model_id.required,
                      },
                    ],
                  })(
                    <Select disabled={mode === 'edit'}>
                      {Object.keys(functions.languages).map(item => (
                        <Option value={item}>{functions.languages[item]}</Option>
                      ))}
                    </Select>
                  )}
                </FormItem>

                <FormItem label="Target" hasFeedback>
                  {getFieldDecorator('tgt', {
                    initialValue: job && job.tgt ? job.tgt : '',
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.model_id.required,
                      },
                    ],
                  })(
                    <Select disabled={mode === 'edit'}>
                      {Object.keys(functions.languages).map(item => (
                        <Option value={item}>{functions.languages[item]}</Option>
                      ))}
                    </Select>
                  )}
                </FormItem>

                {mode !== 'edit' && (
                  <FormItem {...formItemLayout2}>
                    <div className="w-100" error={showError && !fileList.length}>
                      <Dragger {...dprops}>
                        <p className="ant-upload-drag-icon">
                          <Icon type="inbox" />
                        </p>
                        <p className="ant-upload-text">{messages.process.drag}</p>
                      </Dragger>

                      {showError && !fileList.length ? (
                        <Alert
                          style={{ margin: 5 }}
                          message={messages.process.alert}
                          type="error"
                          showIcon
                        />
                      ) : null}
                    </div>
                  </FormItem>
                )}
              </Col>
            </Row>
          </Form>
        </Modal>
      </div>
    </Spin>
  );
};
const WrappedJobsCmp = Form.create()(injectIntl(JobsCmp));

export default withRouter(WrappedJobsCmp);
